import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import "./arrival.css"

import {Link } from 'react-router-dom'

// Import Swiper styles
import React, { useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";

// import required modules
import { Pagination, Navigation,  } from "swiper";

// import data
import new_data1 from "./new_total1"
import new_data2 from "./new_total2"
import new_data3 from "./new_female1"
import new_data4 from "./new_female2"
import new_data5 from "./new_male1"
import new_data6 from "./new_male2"
import new_data7 from "./new_shoes1"
import new_data8 from "./new_shoes2"

function Arrival() {
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  let [data1] = useState(new_data1);
  let [data2] = useState(new_data2);
  let [data3] = useState(new_data3);
  let [data4] = useState(new_data4);
  let [data5] = useState(new_data5);
  let [data6] = useState(new_data6);
  let [data7] = useState(new_data7);
  let [data8] = useState(new_data8);

  let [res1] = useState([0, 1, 2, 3]);
  let [res2] = useState([4, 5, 6, 7]);
  let [res3] = useState([8, 9, 10, 11]);
  let [res4] = useState([12, 13, 14, 15]);
  let [res5] = useState([16, 17, 18, 19]);
  let [res6] = useState([20, 21, 22, 23]);
  let [res7] = useState([24, 25, 26, 27]);
  let [res8] = useState([28, 29, 30, 31]);

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="" {...a11yProps(0)} />
          <Tab label="" {...a11yProps(1)} />
          <Tab label="" {...a11yProps(2)} />
          <Tab label="/" {...a11yProps(3)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <Swiper
          pagination={{
            clickable: true,
          }}
          navigation={true}
          loop={true}
          modules={[Pagination, Navigation]}
          className="mySwiper1"
        >
          <SwiperSlide>
            <All_box1 data1={data1} res1={res1} />
          </SwiperSlide>

          <SwiperSlide>
            <All_box2 data2={data2} res2={res2} />
          </SwiperSlide>
        </Swiper>
      </TabPanel>
      <TabPanel value={value} index={1}>
        <Swiper
          pagination={{
            clickable: true,
          }}
          navigation={true}
          loop={true}
          modules={[Pagination, Navigation]}
          className="mySwiper1"
        >
          <SwiperSlide>
            <All_box3 data3={data3} res3={res3}/>
          </SwiperSlide>

          <SwiperSlide>
            <All_box4 data4={data4} res4={res4}/>
          </SwiperSlide>
        </Swiper>
      </TabPanel>
      <TabPanel value={value} index={2}>
        <Swiper
          pagination={{
            clickable: true,
          }}
          navigation={true}
          loop={true}
          modules={[Pagination, Navigation]}
          className="mySwiper1"
        >
          <SwiperSlide>
            <All_box5 data5={data5} res5={res5}/>
          </SwiperSlide>

          <SwiperSlide>
            <All_box6 data6={data6} res6={res6}/>
          </SwiperSlide>
        </Swiper>
      </TabPanel>
      <TabPanel value={value} index={3}>
        <Swiper
          pagination={{
            clickable: true,
          }}
          navigation={true}
          loop={true}
          modules={[Pagination, Navigation]}
          className="mySwiper1"
        >
          <SwiperSlide>
            <All_box7 data7={data7} res7={res7}/>
          </SwiperSlide>

          <SwiperSlide>
            <All_box8 data8={data8} res8={res8}/>
          </SwiperSlide>
        </Swiper>
      </TabPanel>

    </Box>
  );
}

function All_box1(props) {
  return (
    <>
      {
        props.data1.map((a, i) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
              <Link to={`/detail/${props.res1[i]}`}>
                <img src={a.imgurl} style={{ width: '100%' }} />
                <div className="text_box">
                  <p>{a.title}</p>
                  <p>{a.content}</p>
                  
                  <span className="percent1">{a.sale}</span><span className="original1">{a.original}</span><span className="price1">{a.price}</span>
                </div>
                </Link>
            </div>
          )
        })
      }
    </>
  )
}

