import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import "./best.css"

// Import Swiper styles
import React, { useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Link } from 'react-router-dom'

// import required modules
import { Pagination, Navigation } from "swiper";

// import data
import best_total_data from "./best_total_data"
import best_female_data from "./best_female_data"
import best_male_data from "./best_male_data"
import best_shoes_data from "./best_shoes_data"


function Best() {
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  let [data1] = useState(best_total_data);
  let [data2] = useState(best_female_data);
  let [data3] = useState(best_male_data);
  let [data4] = useState(best_shoes_data);

  let [res12] = useState([56, 57, 58, 59, 60, 61, 62 ,63]);
  let [res13] = useState([64, 65, 66, 67, 68, 69, 70, 71]);
  let [res14] = useState([72, 73, 74, 75, 76, 77, 78, 79]);
  let [res15] = useState([80, 81, 82, 83, 84, 85, 86]);


  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="" {...a11yProps(0)} />
          <Tab label="" {...a11yProps(1)} />
          <Tab label="" {...a11yProps(2)} />
          <Tab label="/" {...a11yProps(3)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0} style={{ height: '1000px' }}>
        <Swiper

          className="mySwiper4"
        >
          <SwiperSlide>
            <All_box1 data1={data1} res12={res12}/>
          </SwiperSlide>


        </Swiper>
      </TabPanel >
      <TabPanel value={value} index={1} style={{ height: '1000px' }}>
        <Swiper

          className="mySwiper4"
        >
          <SwiperSlide>
            <All_box2 data2={data2} res13={res13}/>
          </SwiperSlide>

        </Swiper>
      </TabPanel>
      <TabPanel value={value} index={2} style={{ height: '1000px' }}>
        <Swiper

          className="mySwiper4"
        >
          <SwiperSlide>
            <All_box3 data3={data3} res14={res14}/>
          </SwiperSlide>
        </Swiper>
      </TabPanel>
      <TabPanel value={value} index={3} style={{ height: '1000px' }}>
        <Swiper

          className="mySwiper4"
        >
          <SwiperSlide>
            <All_box4 data4={data4} res15={res15}/>
          </SwiperSlide>
        </Swiper>
      </TabPanel>

    </Box>
  );
}

function All_box1(props) {
  return (
    <>
      {
        props.data1.map((a, i) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
               <Link to={`/detail/${props.res12[i]}`}>
              <img src={a.imgurl} style={{ width: '100%' }} />
              <div className="text_box">
                <p>{a.title}</p>
                <p>{a.content}</p>
                <span class="percent2">{a.sale}</span><span class="original2">{a.original}</span><span class="price2">{a.price}</span>
              </div>
              </Link>
            </div>
          )
        })
      }
    </>
  )
}

function All_box2(props) {
  return (
    <>
      {
        props.data2.map((a, i) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
              <Link to={`/detail/${props.res13[i]}`}>
              <img src={a.imgurl} style={{ width: '100%' }} />
              <div className="text_box">
                <p>{a.title}</p>
                <p>{a.content}</p>
                <span class="percent2">{a.sale}</span><span class="original2">{a.original}</span><span class="price2">{a.price}</span>
              </div>
              </Link>
            </div>
          )
        })
      }
    </>
  )
}


function All_box3(props) {
  return (
    <>
      {
        props.data3.map((a, i) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
              <Link to={`/detail/${props.res14[i]}`}>
              <img src={a.imgurl} style={{ width: '100%' }} />
              <div className="text_box">
                <p>{a.title}</p>
                <p>{a.content}</p>
                <span class="percent2">{a.sale}</span><span class="original2">{a.original}</span><span class="price2">{a.price}</span>
              </div>
              </Link>
            </div>
          )
        })
      }
    </>
  )
}

function All_box4(props) {
  return (
    <>
      {
        props.data4.map((a, i) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
              <Link to={`/detail/${props.res15[i]}`}>
              <img src={a.imgurl} style={{ width: '100%' }} />
              <div className="text_box">
                <p>{a.title}</p>
                <p>{a.content}</p>
                <span class="percent2">{a.sale}</span><span class="original2">{a.original}</span><span class="price2">{a.price}</span>
              </div>
              </Link>
            </div>
          )
        })
      }
    </>
  )
}



function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

export default Best;